* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  background: linear-gradient(to right, rgb(0, 0, 0), rgb(65, 65, 41));
  color: yellow;
  min-height: 100vh;
}

button {
  padding: 10px 20px;
  background-color: black;
  color: yellow;
  border: none;
  border-radius: 10px;
  margin: 0 5px;
  box-shadow: 0 0 1rem gold;
  transition: 0.1s linear;
  cursor: pointer;
  outline: none;
}
button:hover {
  box-shadow: 0 0 2rem gold;
}

.App {
  text-align: center;
}
.todo {
  padding: 10px;
  font-size: 50px;
  margin: 0;
}

.form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100%;
  -webkit-align-items: center;
          align-items: center;
  font-size: 30px;
  padding: 10px;
  border-top: 3px solid yellow;
  border-bottom: 3px solid yellow;
}

.add {
  width: 70%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

input[type="checkbox"] {
  width: 1%;
  margin-right: 10px;
}

input {
  text-align: center;
  margin: 10px;
  width: 50%;
  padding: 5px 10px;
  font-size: 20px;
  background: black;
  color: yellow;
  border-radius: 10px;
  border: 2px solid yellow;
}

label {
  font-size: 16px;
}

input:focus {
  background: yellow;
  color: black;
  outline: none;
}

.btn {
  padding: 10px 50px;
}

.activeTask {
  border-bottom: 2px solid yellow;
  padding: 10px;
}

.activeTask h1 {
  padding: 10px;
}

.completedTasks {
  margin-top: 10px;
}
.completedTasks h1 {
  padding: 10px;
}

.five {
  padding-bottom: 15px;
  color: red;
  font-size: 20px;
}

